<template>
  <div class="login-container" ref="container">
       <div class="login-box" ref="loginBox">
      
          <section class="lb-header lb-animate" ref="loginBoxHeader"></section>
          <section class="lb-body">
             <div class="iptbox lb-animate">
                <i class="ipt-icon"></i>
                <input type="text" name="username" placeholder="USRNAME" class="ipt" v-model="formData.username"/>
             </div>
             <div class="iptbox lb-animate">
                <i class="ipt-icon"></i>
                <input type="password" name="password" placeholder="PASSWORD" class="ipt" v-model="formData.password"/>
             </div>
             <div class="iptbox iptboxBtn lb-animate">
                <input type="button" name="btn" class="ipt_btn" value="LOGIN" @click="confimClick" />
             </div>
             <div class="links-box lb-animate">
                <div class="remember-box" @click="toggleStatus">
                  <i :class="rstatus ? 'selectBox selectBoxActive' : 'selectBox'" ref="selectBox">
                     <font>✓</font>
                  </i>
                  <span class="stext">Remmerber me</span>
                </div>
                <div class="active-link-box">
                  <a href="javascript:;">Register an account.</a>
                </div>
             </div>
          </section>
          <section class="lb-footer lb-animate">
            <span class="bottom-line"></span>
          </section>
      
      </div> 
  </div>
</template>

<script scoped>
  import login from './login.js';
  export default login;
</script>

<!-- Add "scoped" attribute to limit CSS to this component only @import './login.styl' -->
<style scoped lang="stylus">
    @import './login.styl'
</style>


