import toolForm from 'common/js/tool.form.js';
import toolBrowser from 'common/js/tool.browser.js';

const login = {
    data () {
      return {
        login_url: this.$host+'/mcm/api/user/login',
        formData:{
          username: 'movi@skylandertech.com',
          password: '666666'
        },
        rstatus: true,
        loginInfo: null
      }
    },
    methods:{
      //点击进行登陆验证
      confimClick(){
        var username = this.formData.username;
        var password = this.formData.password;

        /*this.$velocity(this.$refs.container,{
            opacity: 0
        },1000,()=>{
            this.$router.push({
                'path': '/'
            });
        })*/
        /*this.$velocity(this.$refs.container,{
            opacity: 0
        },2500,()=>{
            this.$router.push({
                'path': '/'
            });
        })*/

        //离开动画
        this.$velocity($(".lb-animate"), 'transition.slideUpOut', { stagger: 150 , drag: true ,complete: ()=>{
            console.log('animated')
        }});
        
        
        this.$velocity(this.$refs.container,{
            opacity: 0
        },1000,()=>{
            this.$router.push({
                'path': '/'
            });
        });

        return;

        //向服务器验证账户和密码
        this.$axios.post(this.login_url,this.$qs.stringify({
            username: toolForm.strimBothSides(username),
            password: toolForm.strimAll(password)
        })).then((res)=>{

            if(res.status == 200){
              
              this.$Message.success({
                  content: '登陆成功',
                  duration: 1.5,
                  closable: true
              });
              
              //检测是否需要把登陆信息存在本地
              this.loginInfo = res.data;
              if(this.rstatus){
                toolBrowser.lset('loginInfo',this.loginInfo);
              }

            }else{
              this.$Message.error({
                  content: '用户名或密码错误',
                  duration: 1.5,
                  closable: true
              });
            }

             //去到首页
            this.$velocity(this.$refs.container,{
                opacity: 0
            },1000,()=>{
                this.$router.push({
                    'path': '/'
                });
            })

        })
      },
      //切换存储状态
      toggleStatus(){      
        this.rstatus = !this.rstatus;
        if(this.rstatus && this.loginInfo){
            //存本地
            toolBrowser.lset('loginInfo',this.loginInfo);
        }else{
            //清除本地
            toolBrowser.lset('loginInfo','');
        }
      },
      transition(){
        this.$velocity(this.$refs.container,{
            opacity: 1
        },100,()=>{
            
        });
        /*  
        this.$velocity(this.$refs.loginBoxHeader,{
            'opacity': 1
        },650);*/
         
        /*$('.lb-header').addClass('animated fadeInDown');
        $('.iptbox').eq(0).addClass('animated fadeInLeft');
        $('.iptbox').eq(1).addClass('animated fadeInRight');
        $('.iptbox').eq(2).addClass('animated fadeInDown');*/
        this.$velocity($(".lb-header"), 'transition.slideDownIn', { duration: 1850 });
        this.$velocity($('.iptbox').eq(0), 'transition.slideDownIn', { duration: 1400 });
        this.$velocity($('.iptbox').eq(1), 'transition.slideDownIn', { duration: 1100 });
        this.$velocity($('.iptbox').eq(2), 'transition.slideDownIn', { duration: 950 });
        this.$velocity($('.links-box'), 'transition.slideDownIn', { duration: 850 });
        this.$velocity($('.lb-footer'), 'transition.slideDownIn', { duration: 800 });
      }
    },
    created(){
      //console.log(this.$qs);
    },
    mounted(){
        this.transition();
    }
};

export default login;


