/**
 * 表单相关工具函数库
 */
class ToolForm{
    constructor(){

    }

    /**
     * [清除字符串两边的空格]
     * @param  {[String]} str [要处理的字符串]
     * @return {[String]}     [经过处理好的字符串]
     */
    strimBothSides(str){
        let newStr = '';
        if(typeof String.prototype.trim === 'function'){
            newStr = str.trim();
        }else{
            newStr = str.replace(/(^\s*)|(\s*$)/g, "");
        }
        return newStr;
    }

    /**
     * [strimAll 清除字符串中所有的空格]
     * @param  {[String]} str [要处理的字符串]
     * @return {[String]}     [经过处理好的字符串]
     */
    strimAll(str){
        let result = '';
        result = str.replace(/\s/g,"");
        return result;
    }


}


export default new ToolForm();


